import React ,{useState,useEffect}from "react";
import './navbar.css';
import { Link, NavLink } from "react-router-dom";
import {useSelector,useDispatch} from 'react-redux';
import changeCountry, { changeLang, changeLight } from '../../store/action/action'
import { useTranslation } from 'react-i18next';
import i18next from "i18next";



export default function Navbar() {

    let lang=useSelector((state)=>state.lang)
    const [t]=useTranslation();
    const dispatch=useDispatch();
    
    const handleCountryChange = (event)=>{
        let newCountrys = event.target.value;
        dispatch(changeCountry(newCountrys));
    }
    const handleChangeLang=(event)=>{
        let newLang=event.target.value;
        dispatch(changeLang(newLang));
        i18next.changeLanguage(newLang)
    }
    const handleChangelight=(event)=>{


        let newColor=event.target.checked;
        dispatch(changeLight(newColor))
    }




    return(
        <>
            <nav className="navbar navbar-expand-lg bg-color fixed-top ">
                <div className="container-fluid w-80">
                    <Link className="navbar-brand" to="home"> {t('A-S-News')} </Link>
                    <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
                        <span className="navbar-toggler-icon"></span>
                    </button>
                    <div className="collapse navbar-collapse" id="navbarSupportedContent">
                        <ul className="navbar-nav ms-auto mb-2 mb-lg-0" dir={`${lang=="en"?'ltr':'rtl'}`}>
                            <li className="nav-item">
                                <NavLink className="nav-link "  to="home">{t('Home')}</NavLink>
                            </li>
                            <li className="nav-item">
                                <NavLink className="nav-link" to="technology">{t('Technology')}</NavLink>
                            </li>
                            <li className="nav-item">
                                <NavLink className="nav-link" to="sports">{t('Sports')}</NavLink>
                            </li>
                            <li className="nav-item">
                                <NavLink className="nav-link" to="health">{t('Health')}</NavLink>
                            </li>
                            <li className="nav-item">
                                <NavLink className="nav-link" to="business">{t('Business')}</NavLink>
                            </li>
                            <li className="nav-item">
                                <NavLink className="nav-link" to="science">{t('Science')}</NavLink>
                            </li>
                            <li className="nav-item">
                                <NavLink className="nav-link" to="entertainment">{t('Entertainment')}</NavLink>
                            </li>
                        </ul>
                        <aside className=" d-flex  align-items-center">
                            <div className="me-4">
                                <select onChange={handleChangeLang} className="form-control" name="language" id="language">
                                    <option value={"en"}>EN</option>
                                    <option value={"ar"}>AR</option>
                                </select>
                            </div>

                            <div className="me-4">
                                <select onChange={handleCountryChange} className="form-control" name="country" id="country">
                                    <option value={"us"}>US</option>
                                    <option value={"eg"}>EG</option>
                                    <option value={"za"}>ZA</option>
                                    <option value={"ae"}>AE</option>
                                    <option value={"ar"}>AR</option>
                                    <option value={"at"}>AT</option>
                                    <option value={"au"}>AU</option>
                                    <option value={"be"}>BE</option>
                                    <option value={"bg"}>BG</option>
                                    <option value={"br"}>BR</option>
                                    <option value={"ca"}>CA</option>
                                    <option value={"ch"}>CH</option>
                                    <option value={"cn"}>CN</option>
                                    <option value={"co"}>CO</option>
                                    <option value={"cu"}>CU</option>
                                    <option value={"cz"}>CZ</option>
                                    <option value={"eg"}>EG</option>
                                    <option value={"fr"}>FR</option>
                                    <option value={"gb"}>GB</option>
                                    <option value={"gr"}>GR</option>
                                    <option value={"hk"}>HK</option>
                                    <option value={"hu"}>HU</option>
                                    <option value={"id"}>ID</option>
                                    <option value={"ie"}>IE</option>
                                    <option value={"il"}>IL</option>
                                    <option value={"in"}>IN</option>
                                    <option value={"it"}>IT</option>
                                    <option value={"lv"}>LV</option>
                                    <option value={"ma"}>MA</option>
                                    <option value={"mx"}>MX</option>
                                    <option value={"my"}>MY</option>
                                    <option value={"ng"}>NG</option>
                                    <option value={"nl"}>NL</option>
                                    <option value={"no"}>NO</option>
                                    <option value={"nz"}>NZ</option>
                                    <option value={"ph"}>PH</option>
                                    <option value={"pl"}>PL</option>
                                    <option value={"pt"}>PT</option>
                                    <option value={"ro"}>RO</option>
                                    <option value={"rs"}>RS</option>
                                    <option value={"ru"}>RU</option>
                                    <option value={"sa"}>SA</option>
                                    <option value={"se"}>SE</option>
                                    <option value={"sg"}>SG</option>
                                    <option value={"si"}>SI</option>
                                    <option value={"sk"}>SK</option>
                                    <option value={"th"}>TH</option>
                                    <option value={"tr"}>TR</option>
                                    <option value={"tw"}>TW</option>
                                    <option value={"ua"}>UA</option>
                                    <option value={"ve"}>VE</option>
                                </select>
                            </div>
                            
                            <label className="switch-name">
                                <input type="checkbox" onChange={handleChangelight} className="checkbox"/>
                                <div className="back"></div>
                                <svg className="moon" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 384 512">
                                    <path d="M223.5 32C100 32 0 132.3 0 256S100 480 223.5 480c60.6 0 115.5-24.2 155.8-63.4c5-4.9 6.3-12.5 3.1-18.7s-10.1-9.7-17-8.5c-9.8 1.7-19.8 2.6-30.1 2.6c-96.9 0-175.5-78.8-175.5-176c0-65.8 36-123.1 89.3-153.3c6.1-3.5 9.2-10.5 7.7-17.3s-7.3-11.9-14.3-12.5c-6.3-.5-12.6-.8-19-.8z"></path>
                                </svg>
                                <svg className="sun" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 512 512">
                                    <path d="M361.5 1.2c5 2.1 8.6 6.6 9.6 11.9L391 121l107.9 19.8c5.3 1 9.8 4.6 11.9 9.6s1.5 10.7-1.6 15.2L446.9 256l62.3 90.3c3.1 4.5 3.7 10.2 1.6 15.2s-6.6 8.6-11.9 9.6L391 391 371.1 498.9c-1 5.3-4.6 9.8-9.6 11.9s-10.7 1.5-15.2-1.6L256 446.9l-90.3 62.3c-4.5 3.1-10.2 3.7-15.2 1.6s-8.6-6.6-9.6-11.9L121 391 13.1 371.1c-5.3-1-9.8-4.6-11.9-9.6s-1.5-10.7 1.6-15.2L65.1 256 2.8 165.7c-3.1-4.5-3.7-10.2-1.6-15.2s6.6-8.6 11.9-9.6L121 121 140.9 13.1c1-5.3 4.6-9.8 9.6-11.9s10.7-1.5 15.2 1.6L256 65.1 346.3 2.8c4.5-3.1 10.2-3.7 15.2-1.6zM160 256a96 96 0 1 1 192 0 96 96 0 1 1 -192 0zm224 0a128 128 0 1 0 -256 0 128 128 0 1 0 256 0z"></path>
                                </svg>
                            </label>
                        </aside>
                    </div>
                </div>
            </nav>
        </>
    )
}